@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.main-container{
    position: relative;
    width: 100%;
    height: 100%;
    /* overflow-y: scroll; */
    overflow-y: overlay;
}
.main-container::-webkit-scrollbar{
    width: 5px;
    /* background-color: black; */
}
.main-container::-webkit-scrollbar-thumb{
    width: 5px;
    background-color: gray;
}
.mainscreen-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #2941ab; */
    background-color: #151529;
    /* background-color: white; */
}
.mainscreen{
    position: relative;
    width: 1800px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #1ed760; */
    /* background-color: #339dff; */
    /* background-color: #066bff; */
    /* background-color: #2941ab; */
    background-color: transparent;
    /* background-color: rgb(3, 99, 37); */
}

.p-content{
    width: 80%;
    height: auto;
    color: #1ed760;
    font-size: 10em;
    font-weight: 700;
    letter-spacing: -5px;
    padding-right: 10px;
    text-align: center;
}

/* .innermain{
    width: 1600px;
    height: 100%;
    background-color: black;
    padding: 0px 120px;
    padding-top: 80px;
} */

.middle-container{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 70%;
    height: 400px;
    /* background-color: blue; */
}

.center{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    /* background-color: #1ed760; */
}
.center p{
    text-align: center;
    font-size: 5em;
    font-family: "anton";
    color: white;
}
.center button{
    margin-top: 50px;
    width: 500px;
    height: 50px;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 25px;
    color: white;
    cursor: pointer;
    transition: background-color 0.1s ease-in-out, border 0.1s ease-in-out;
}
.center button:hover{
    background-color: #066bff;
    border: none;
}
.logo-part{
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #1ed760; */
}
.logo-part img{
    width: auto;
    height: 35%;
    margin-right: 5px;
    border-radius: 50%;
}
.logo-part p{
    font-size: 2em;
    font-family: "anton";
    color: white;
}